<template>
  <div class="wrap">
    <van-nav-bar
      title="报表"
      left-text="返回"
      left-arrow
      @click-left="goBackFn"
    >
    </van-nav-bar>
    <div class="main-content">
      <van-cell-group v-show="show">
        <van-cell title="质量报表" @click="onSelect(1)" is-link />
        <van-cell title="品质报表" @click="onSelect(2)" is-link />
      </van-cell-group>
      <div class="chart_wrap">
        <div id="myChart" ref="myChart" class="chart"></div>
      </div>
    </div>
    <div class="bottom-bar">
      <bottom-bar :active="3"></bottom-bar>
    </div>
  </div>
</template>

<script>
import { NavBar, Actionsheet, Cell, CellGroup } from "vant";
export default {
  name: "Report",
  components: {
    [NavBar.name]: NavBar,
    [Actionsheet.name]: Actionsheet,
    [Cell.name]: Cell,
    [CellGroup.name]: CellGroup
  },
  data() {
    return {
      show: true
    };
  },
  methods: {
    goBackFn() {
      this.$router.go(-1);
    },
    drawChart() {
      let myChart = this.$echarts.init(document.getElementById("myChart"));
      myChart.setOption({
        tooltip: {},
        toolbox: {},
        legend: {
          data: ["总数", "累计占比率"]
        },
        xAxis: [
          {
            type: "category",
            data: ["VC", "AS", "ZC", "ET", "WQ", "FM"]
          }
        ],
        yAxis: [
          {
            type: "value",
            name: "总数",
            min: 0,
            max: 30,
            interval: 5,
            axisLabel: {
              formatter: "{value}"
            }
          },
          {
            type: "value",
            name: "累计占比率",
            min: 0,
            max: 120,
            interval: 20,
            axisLabel: {
              formatter: "{value}%"
            }
          }
        ],
        series: [
          {
            name: "数量",
            type: "bar",
            data: [25, 14, 11, 6, 5, 5],
            itemStyle: {
              normal: {
                color: "#37A2DA"
              }
            }
          },
          {
            name: "占比率",
            type: "line",
            yAxisIndex: 1,
            data: [15, 30, 60, 67, 73, 85]
          }
        ]
      });
    },
    onSelect(type) {
      if (type === 1) {
        this.show = false;
        this.drawChart();
      } else if (type === 2) {
        this.$toast("品质报表暂未开发");
      }
    }
  }
};
</script>

<style lang="scss" scoped>
.wrap {
  height: 100%;
  width: 100%;
  display: flex;
  flex-direction: column;
  .main-content {
    flex: 1;
    position: relative;
    display: flex;
    flex-direction: column;
    width: 100%;
    .chart_wrap {
      width: 100%;
      flex: 1;
      height: 0;
      overflow-y: auto;
      position: relative;
      .chart {
        position: absolute;
        right: 0;
        height: 100%;
        padding: 0;
        padding-left: 5px;
        border: none;
        z-index: 30;
        background: #f3f3f3;
        left: 0;
      }
    }
  }
  .bottom-bar {
    height: 50px;
  }
}
</style>
